import React, { Component } from 'react';

class MailInput extends Component{
    
    render(){
        const styleProp = {
            input: {
                border: "none",
                borderBottom: "2px solid black",
                width: window.innerWidth*0.8,
                padding: 9,
                marginBottom: 20,
                background: "rgb(240, 240, 240)",
                outline: "none"
            }
        }

        return (
            <div>
                <input style={styleProp.input} type="email" placeholder={this.props.placeholder} />
            </div>
        );
    }
}

class NormalInput extends Component{
    
    render(){
        const styleProp = {
            input: {
                border: "none",
                borderBottom: "2px solid black",
                width: window.innerWidth*0.8,
                padding: 9,
                marginBottom: 20,
                background: "rgb(240, 240, 240)",
                outline: "none"
            }
        }

        return (
            <div>
                <input style={styleProp.input} type="text" placeholder={this.props.placeholder} />
            </div>
        );
    }
}

class MessageInput extends Component{
    
    render(){
        const styleProp = {
            input: {
                border: "none",
                borderBottom: "2px solid black",
                width: window.innerWidth*0.8,
                padding: 9,
                marginBottom: 20,
                background: "rgb(240, 240, 240)",
                outline: "none"
            }
        }

        return (
            <div>
                <textarea style={styleProp.input} type="text" placeholder={this.props.placeholder} ></textarea>
            </div>
        );
    }
}

class Input extends Component{

    render(){
        switch(this.props.type){
            case "email": return (<MailInput placeholder={this.props.placeholder}/>);break;
            case "normal": return (<NormalInput placeholder={this.props.placeholder}/>);break;
            case "message": return (<MessageInput placeholder={this.props.placeholder}/>);break;
        }
    }
  }
  
  export default Input;