import React, { Component } from 'react';

class Form extends Component{

    render(){

        const styleProp = {
            padding: window.innerWidth * 0.05,
            paddingTop: 0,
        }

        return (
            <form style={styleProp}>
                {this.props.children}
            </form>
        );
    }
  }
  
  export default Form;