import React, { Component } from 'react';

class SimpleBox extends Component{
    render(){
        
        const pStyle = {
            background: this.props.background,
            color: this.props.color
        }

        const stylesType = {
            box: {
                padding: window.innerWidth * 0.05,
                paddingTop: window.innerHeight * 0.4,
                paddingBottom: window.innerHeight * 0.2,
            }, 
            
            h3: {
                fontSize: (window.innerHeight * 0.05)
            }
        }

        const bData = (
            <div style={stylesType.box}>
                <h3 style={stylesType.h3}>{this.props.title}<br/>
                <small>{this.props.description}</small></h3>
            </div>
        );

        return (
            <div style={pStyle}>
                {bData}
            </div>
        );
    }
}

