class SimpleP extends Component{
    constructor(){
        super();
        this.state = {
            menuActive: 0,
            updateValues: 0
        };
    }

    componentDidMount(){
        //window.addEventListener("scroll", this.onNeedsUpdate);
    }

    componentWillUnmount(){
        //window.removeEventListener("scroll", this.onNeedsUpdate);
    }

    onNeedsUpdate = () => {
        this.setState({updateValues: 1});
    }

    render(){
        
        const pStyle = {
        }

        const wvar = {
            "left": (window.innerWidth > 400)?(window.innerWidth*0.3)-10:window.innerWidth*0.8,
            "right": (window.innerWidth > 400)?(window.innerWidth*0.3)-10:window.innerWidth*0.8,
            "top": (window.innerWidth > 400)?window.innerWidth*0.8:window.innerWidth*0.8,
            "bottom": (window.innerWidth > 400)?window.innerWidth*0.8:window.innerWidth*0.8,
        }

        const w2var = {
            "left": (window.innerWidth > 400)?(window.innerWidth*0.6)-10:window.innerWidth*0.8,
            "right": (window.innerWidth > 400)?(window.innerWidth*0.6)-10:window.innerWidth*0.8,
            "top": (window.innerWidth > 400)?window.innerWidth*0.8:window.innerWidth*0.8,
            "bottom":(window.innerWidth > 400)? window.innerWidth*0.8:window.innerWidth*0.8,
        }

        const fvar = {
            "left": "left",
            "right": "right",
            "top": "auto",
            "bottom": "auto",
        }

        const stylesType = {
            content:{
                textAlign: (
                    (window.innerWidth > 400)?
                    "center":"left"
                ),
                background: this.props.background
            },
            box: {
                padding:  (
                    (this.props.folder != null) ?
                    (
                        (window.innerWidth > 400)?
                        window.innerHeight*0.1:30
                    ):(
                        (window.innerWidth > 400)?
                        window.innerHeight*0.3:30
                    )
                ),

                display: (
                    (this.props.folder != null) ?
                    "inline-table":"block"
                ),

                width: (
                    (this.props.folder != null) ?
                    (w2var[this.props.position]):"auto"
                ),

                height: (
                    (this.props.folder != null) ?
                    "auto":"auto"
                )
            }, 
            
            h3: {
                fontSize: (window.innerWidth > 400)?(
                    (this.props.folder != null) ?
                    window.innerHeight*0.05:window.innerHeight*0.07
                ):(
                    (this.props.folder != null) ?
                    window.innerHeight*0.03:window.innerHeight*0.04
                ),
            },

            folder: {

                float: (
                    (this.props.folder != null) ?
                    fvar[this.props.position]:"auto"
                ),

                display: (
                    (this.props.folder != null) ?
                    "inline-table":"block"
                ),

                background: (
                    (this.props.folder != null) ?
                    this.props.folder:"transparent"
                ),

                width: (
                    (this.props.folder != null) ?
                    wvar[this.props.position]:"auto"
                ),

                height: (
                    (this.props.folder != null) ?
                    window.innerHeight*0.5:"auto"
                ),
                
                margin:  (
                    (this.props.folder != null) ?
                    30:0
                ),
            }
        }

        const bData = (
            <div style={stylesType.content}>
                <div style={stylesType.folder}></div>
                <div style={stylesType.box}>
                    <h3 style={stylesType.h3}>{this.props.title}<br/>
                    <small>{this.props.description}</small></h3>
                </div>
            </div>
        );

        return (
            <div style={pStyle}>
                {bData}
            </div>
        );
    }
}

class ShowBox extends Component{
    render(){
        const pStyle = {
            background: this.props.background,
            color: this.props.color,
            width: ((
                window.innerWidth > 700
            )? window.innerWidth * 0.25 - 5:"auto"),
            height: window.innerHeight * 0.8,
            display: "inline-table",
        }

        const stylesType = {
            box: {
                padding: window.innerWidth * 0.05,
                paddingTop: window.innerHeight * 0.1,
                paddingBottom: window.innerHeight * 0.1,
            }, 
            
            h3: {
                fontSize: (window.innerHeight * 0.04)
            }
        }

        const bData = (
            <div style={stylesType.box}>
                <h3 style={stylesType.h3}>{this.props.title}<br/>
                <small>{this.props.description}</small></h3>
            </div>
        );

        return (
            <div style={pStyle}>
                {bData}
            </div>
        );
    }
}

class Box extends Component{


    render(){
        const background = (
                "rgb("+this.props.r+","
                +this.props.g+","
                +this.props.b+")"
        );

        const color = (
                ((
                    Number(this.props.r)+
                    Number(this.props.g)+
                    Number(this.props.b)
                ) < ((255/2)*3)) ?
                "white":"black"
            );

        switch(this.props.boxType){
            case "SimpleBox": return (<SimpleBox background={background} color={color} title={this.props.title} description={this.props.description} />); break;
            case "SBDesc": return (<SBDesc background={background} color={color} title={this.props.title} description={this.props.description} />); break;
            case "ShowBox": return (<ShowBox background={background} color={color} title={this.props.title} description={this.props.description}/>); break;
            case "Project": return (<Project background={background} color={color} title={this.props.title} description={this.props.description} base={this.props.base}/>); break;
            case "SimpleP": return (<SimpleP background={background} color={color} title={this.props.title} description={this.props.description} base={this.props.base} folder={this.props.folder} position={this.props.position}/>); break;
            default: return (<SimpleBox background={background} color={color} title={this.props.title} description={this.props.description} />); break;
        }

    }
  }
  
  export default Box;