import React, { Component } from 'react';

class Submit extends Component{

    render(){

        const styleProp = {
            submit: {
                border: "none",
                borderBottom: "2px solid black",
                padding: 9,
                marginBottom: 20,
                background: "black",
                color: "white"
            }
        }

        return (
            <div class="form-group">
                <input type="submit" style={styleProp.submit} value={this.props.value} />
            </div>
        );
    }
  }
  
  export default Submit;