import React, { Component } from 'react';
import List from "./List";
import { Z_FIXED } from 'zlib';

class Header extends Component {
    
    constructor(){
        super();
        this.state = {
            menuActive: 0,
            updateValues: 0
        };
    }

    componentDidMount(){
        window.addEventListener("resize", this.onNeedsUpdate);
    }

    componentWillUnmount(){
        window.removeEventListener("resize", this.onNeedsUpdate);
    }

    onNeedsUpdate = () => {
        this.setState({updateValues: 1});
    }

    onMenuClicked = () => {
        const {menuActive} = this.state;
        this.setState({ menuActive:  !menuActive});
    }

    render(){
        const {menuActive} = this.state;

        const styles = {
            logo: {
                padding: 10,
                paddingTop: 20,
                paddingBottom: 10,
                fontSize: 20,
                background: "rgb(40, 40, 40)",
                color: "white"
            },

            header:{
                position: "fixed",
                width: window.innerWidth
            },

            menu:{
                position: "fixed",
                left: ((
                    window.innerWidth > 700
                )? window.innerWidth * 0.9:window.innerWidth * 0.8),
                width: ((
                    window.innerWidth > 700
                )? window.innerWidth * 0.1:window.innerWidth * 0.2),
                background: "rgb(60, 60, 60)",
                top: 0,
                padding: 10,
                paddingTop: 20,
                paddingBottom: 9,
                cursor:"pointer"
            },

            menuList: {
                position: ((
                    window.innerWidth > 700
                )? "fixed":"auto"),
                height: window.innerHeight,
                background: "rgb(240, 240, 240)",
                width: ((
                    window.innerWidth > 700
                )? window.innerWidth*0.4:"auto"),
                left: ((
                    window.innerWidth > 700
                )? window.innerWidth*0.6:"auto"),
            }
        };

        const marginOffset = {
            height: (
                styles.logo.paddingTop + 
                styles.logo.paddingBottom +
                styles.logo.fontSize
            )
        }

        const menuData = (
            <div class="menu" style={styles.menuList}>
                <List href="/" value="Início" />
                <List href="/tutoriais" value="Tutoriais" />
                <List href="/blog" value="Blog" />
            </div>
        );

        /*
        
                        <div onClick={this.onMenuClicked} style={styles.menu}>
                            <i class="material-icons">
                                menu
                            </i>
                        </div>
                        */

        return (
            <div>
                <div style={styles.header}>
                    <div style={styles.logo} class="title">
                        <div className="logo">
                            gabriel<b>vanderlei</b>
                        </div>
                    </div>
                    { menuActive ? menuData: "" }
                </div>
                <div style={marginOffset}></div>
            </div>
        );
    }
  }

  export default Header;