import React, { Component } from 'react';
import '../css/List.css';
import { redBright } from 'ansi-colors';

class List extends Component{
    constructor() {
        super();
        this.state = {
            path: window.location.pathname,
        };
    }

    render(){
        const styles = {
            list: {
                background: "rgb(240, 240, 240)",
                padding: 10,

                link: {
                    color: "black"
                },
                
                selected: {
                    background: "rgb(250, 250, 250)",
                    padding: 10
                }
            }
        }

        const {path} = this.state;
        if(path === this.props.href){

            return (
                <div style={styles.list.selected}>
                <a style={styles.list.link}  href={this.props.href}>{this.props.value}</a>
                </div>
            );
        } else{
            return (
                <div style={styles.list} >
                <a style={styles.list.link} href={this.props.href}>{this.props.value}</a>
                </div>
            );
        }
    }
  }
  
  export default List;